@media screen and (max-width: 500px) {
  html,
  body {
    overflow-x: hidden;
  }
  .main {
    width: 300%;
    height: 100vh;
    margin-left: -100%;
  }
  .sidebar {
    width: 100%;
    min-height: 100vh !important;
    z-index: 11;
    margin-left: 0%;
  }
  .sidebar .container .arrowLeft {
    display: block;
  }
  .sidebar .nav-link {
    padding: 2% 2%;
    margin-top: 3% !important;
  }
  .posts {
    width: 100%;
    z-index: 1;
    padding-top: 7%;
  }
  .topbar-for-mobile {
    width: 99.4%;
    left: 0;
    top: 0;
    transform: translate(0);
    display: flex;
    z-index: 6;
  }
  .posts .all-posts {
    padding: 0;
  }
  .posts .all-posts .particular-post {
    width: 95%;
  }
  .posts .all-posts .particular-post .heading h6 {
    font-size: 1rem;
  }
  .posts .all-posts .particular-post .heading p {
    font-size: 0.8rem;
  }
  .particular-post .icons i {
    font-size: 1.3rem;
  }
  .particular-post .icons span {
    font-size: 1rem;
    margin-top: 1%;
  }
  .messagebar {
    /* position: absolute;
    top: 0;
    left: 0; */
    width: 100%;
    z-index: 111;
  }
  .messagebar .top h6 {
    margin-left: 12%;
  }
  .messagebar .top .arrowLeft {
    display: block;
    left: 3%;
  }
  .messagebar .list .item {
    padding: 0 3%;
  }
  .messagebar .list .item .content {
    height: 100%;
    display: flex;
    justify-content: center;
  }
  .messagebar .list .item .title {
    line-height: 1.5rem;
    margin-top: 0.1 rem;
  }
  .messagebar .list .item p {
    line-height: 0rem;
  }
  .searchSection {
    width: 100%;
    z-index: 1;
    padding: 7% 0% 0 0%;
  }
  .searchSection .wrraper-1 {
    grid-column-gap: 0.4em;
    grid-template-columns: 40% 60%;
    display: grid;
    justify-content: center;
    padding: 0 3%;
  }
  .searchSection .wrraper-2 {
    grid-column-gap: 0.4rem;
    grid-template-columns: 40% 60%;
    display: grid;
    justify-content: center;
    padding: 0 3%;
  }
  .searchSection .input-group {
    width: 95%;
    text-align: center;
    margin: 0 auto 4% auto;
  }
  .searchSection .input-group .form-control {
  }
  .reels {
    width: 100%;
  }
  .reels .overlay {
    height: 32%;
  }
  .reels .overlay .profile-detail {
    margin-top: 10%;
  }
  .reels .overlay .profile-detail img {
    width: 35px;
    height: 35px;
    margin: auto 0;
  }
  .reels .overlay .profile-detail h6,
  a {
    font-size: 0.9rem;
  }
  .reels p {
    font-size: 0.8rem;
  }
  .igtv {
    width: 100%;
  }
  .igtv .overlay {
    height: 32%;
  }
  .igtv .overlay .profile-detail {
    margin-top: 10%;
  }
  .igtv .overlay .profile-detail img {
    width: 35px;
    height: 35px;
    margin: auto 0;
  }
  .igtv .overlay .profile-detail h6,
  a {
    font-size: 0.9rem;
  }
  .igtv p {
    font-size: 0.8rem;
  }
  .activity {
    width: 100%;
    padding-top: 7%;
  }
  .activity .follow-item img {
    width: 20%;
  }
  .activity .follow-item h6 {
    line-height: 1.7rem;
  }
  .activity .follow-item p {
    line-height: 1rem;
  }
  .activity .following-item {
    padding: 2%;
  }
  .activity .btn {
    font-size: 0.9rem;
    padding: 1.5% 3% !important;
  }
  .settings {
    width: 100%;
    padding-top: 7%;
  }
}
